// app/product/[id]/components/ProductGalleryClient.tsx
'use client';
import React, { useEffect, useRef, useState } from 'react';
import { Carousel, Modal, Image } from 'antd';

type Props = {
  images: string[]; // url list
  title?: string;
};

export default function ProductGalleryClient({ images = [], title }: Props) {
  const [index, setIndex] = useState(0);
  const [autoPlay, setAutoPlay] = useState(true);
  const [open, setOpen] = useState(false);
  const [modalIndex, setModalIndex] = useState(0);
  const carouselRef = useRef<any>(null);

  useEffect(() => {
    if (!autoPlay || !carouselRef.current) return;
    const interval = setInterval(() => {
      setIndex((i) => (i + 1) % Math.max(1, images.length));
    }, 4500);
    return () => clearInterval(interval);
  }, [autoPlay, images.length]);

  useEffect(() => {
    // sync to Carousel's goTo if available
    if (carouselRef.current?.goTo) {
      carouselRef.current.goTo(index);
    }
    // update thumbs active class via state-driven class on markup below
  }, [index]);

  function openModalAt(i: number) {
    setModalIndex(i);
    setOpen(true);
  }

  return (
    <>
      <div className="carousel" style={{ position: 'relative' }}>
        <div className="product-contact-cta interactive" role="region" aria-label="Contact our studio about this piece" tabIndex={0}>
          <div className="pc-cta-text">Curious about finish or framing?</div>
          <a className="pc-contact-link interactive" href="#" id="contactStudio">Ask our studio</a>
        </div>

        <div className="slides-wrap" onMouseEnter={() => setAutoPlay(false)} onMouseLeave={() => setAutoPlay(true)}>
          <Carousel ref={carouselRef} dots={false} afterChange={(current) => setIndex(current)}>
            {images.map((src, i) => (
              <div key={src} className="slide">
                <div className="image-wrap">
                  <img
                    src={src}
                    alt={title ?? `Image ${i + 1}`}
                    data-index={i}
                    onClick={() => openModalAt(i)}
                    style={{ cursor: 'zoom-in', width: '100%', height: '100%', objectFit: 'cover' }}
                  />
                </div>
              </div>
            ))}
          </Carousel>
        </div>

        <div className="indicator" style={{ position: 'absolute', left: 12, bottom: 12 }}>{index + 1} / {Math.max(1, images.length)}</div>

        <button className="nav-arrow arrow-left interactive accent-hover" aria-label="Previous image" onClick={() => setIndex((i) => (i - 1 + images.length) % images.length)}>
          <svg width="16" height="16" viewBox="0 0 24 24" fill="none"><path d="M15 18l-6-6 6-6" stroke="#eaf6f0" strokeWidth="1.6" strokeLinecap="round" strokeLinejoin="round"/></svg>
        </button>

        <button className="nav-arrow arrow-right interactive accent-hover" aria-label="Next image" onClick={() => setIndex((i) => (i + 1) % images.length)}>
          <svg width="16" height="16" viewBox="0 0 24 24" fill="none"><path d="M9 6l6 6-6 6" stroke="#eaf6f0" strokeWidth="1.6" strokeLinecap="round" strokeLinejoin="round"/></svg>
        </button>
      </div>

      <div className="thumbs" style={{ marginTop: 14 }}>
        {images.map((src, i) => (
          <div
            key={src}
            className={`thumb ${i === index ? 'active' : ''}`}
            data-index={i}
            tabIndex={0}
            role="button"
            aria-label={`View image ${i + 1}`}
            onClick={() => setIndex(i)}
            onKeyDown={(e) => { if (e.key === 'Enter' || e.key === ' ') { e.preventDefault(); setIndex(i); } }}
          >
            <img src={src} alt="" />
          </div>
        ))}
      </div>

      <Modal
        open={open}
        onCancel={() => setOpen(false)}
        footer={null}
        centered
        width="80%"
        style={{ background: 'transparent', textAlign: 'center' }}
        closeIcon={<span />}
      >
        <div style={{ display: 'flex', gap: 12, alignItems: 'center' }}>
          <div style={{ flex: 1 }}>
            <Image src={images[modalIndex]} alt={`Enlarged ${modalIndex + 1}`} style={{ maxHeight: '70vh', objectFit: 'contain' }} />
          </div>
          <div style={{ width: 120, display: 'flex', flexDirection: 'column', gap: 8, overflowY: 'auto' }}>
            {images.map((src, i) => (
              <img
                key={src}
                src={src}
                alt=""
                style={{ width: '100%', height: 70, objectFit: 'cover', borderRadius: 8, border: i === modalIndex ? `2px solid var(--accent2)` : '2px solid transparent', cursor: 'pointer' }}
                onClick={() => setModalIndex(i)}
              />
            ))}
          </div>
        </div>
      </Modal>
    </>
  );
}